import React, { PureComponent } from 'react';
import { Icon, message } from 'antd';
import FormItemRender from './FormItemRender';
import FormItemEdit from '../../EditDrawer/FormItemEdit';
import { activeColor } from '../../../utils/Constants';
import initDropData from '../../../utils/DropUtil';
import './index.css';

const clsName = 'drop-container border-box';
const forbiddenDropType = ['Breadcrumb', 'Text', 'Tabs'];

class FormItemContainer extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            dropComponent: !!props.configs.type,
        };
        this.btnIndex = 1;
        this.onUpdateConfigs = this.onUpdateConfigs.bind(this);
        this.onShowEditItem = this.onShowEditItem.bind(this);
        this.onEditDropContainer = this.onEditDropContainer.bind(this);
        this.onDrop = this.onDrop.bind(this);
        this.onDeleteChildren = this.onDeleteChildren.bind(this);
        this.onClose = this.onClose.bind(this);
    }
    onShowEditItem() {
        this.setState({
            visible: true,
        });
    }
    onDrop(event) {
        event.preventDefault();
        if (event.target.className === clsName) {
            this.resetBg();
            const data = JSON.parse(event.dataTransfer.getData('text/plain'));
            const { type } = data;
            if (~forbiddenDropType.indexOf(type)) {
                message.warn('!');
                return;
            }
            const { onUpdateFormItemConfigs, colIndex } = this.props;
            const newConfigs = initDropData(data, this, true);
            if (newConfigs) {
                this.setState({
                    dropComponent: true,
                }, () => {
                    onUpdateFormItemConfigs({ colIndex, ...newConfigs });
                });
            }
        }
    }
    /**
     * @desc  
     * @param {object} e 
     */
    onDeleteChildren(e) {
        e && e.stopPropagation();
        const { onUpdateFormItemConfigs, colIndex } = this.props;
        this.setState({
            dropComponent: false,
        });
        onUpdateFormItemConfigs({ colIndex, deleteFlag: true });
    }
    resetBg() {
        const dropDomArr = document.querySelectorAll('.form-container .drop-container');
        if (dropDomArr) {
            dropDomArr.forEach((item) => {
                item.style.background = '';
            });
        }
    }
    onDragenter(event) {
        if (event.target.className === clsName) {
            event.target.style.background = activeColor;
        }
    }
    onDragLeave(event) {
        if (event.target.className === clsName) {
            event.target.style.background = '';
        }
    }
    componentWillUnmount() {
        if (this.targetDom) {
            this.targetDom.removeEventListener('drop', this.onDrop);
            this.targetDom.removeEventListener('dragleave', this.onDragLeave);
            this.targetDom.removeEventListener("dragenter", this.onDragenter);
        }
    }
    componentDidMount() {
        if (!this.targetDom) {
            return;
        }
        this.targetDom.addEventListener("drop", this.onDrop, false);
        this.targetDom.addEventListener("dragleave", this.onDragLeave, false);
        this.targetDom.addEventListener("dragenter", this.onDragenter, false);
    }
    onUpdateConfigs(newConfigs) {
        const { configs } = this.props;
        const { colIndex } = configs;
        this.setState({
            dropComponent: !newConfigs.deleteFlag,
        }, () => {
            const { onUpdateFormItemConfigs } = this.props;
            onUpdateFormItemConfigs(Object.assign(configs, newConfigs, { colIndex }));
            this.onClose();
        });
    }
    onEditDropContainer(e) {
        if (e.target.className === clsName) {
            e && e.stopPropagation();
            this.setState({
                visible: true,
            });
        }
    }
    onClose() {
        this.setState({
            visible: false,
        });
    }
    getContainerWidth(colSpan = 8) {
        return `${100 * colSpan / 24 }%`;
    }
    render() {
        const { layoutColumn, onDeleteFormItemContainer,  preview = false, colSpanArr = [], formItemArr = [], configs } = this.props;
        const { dropComponent, visible } = this.state;
        const { colIndex } = configs;
        const colIndexNum = +colIndex;
        const _colSpan = Object.hasOwnProperty.call(configs, 'colSpan') ? 
            configs.colSpan : (24 / layoutColumn);
        return (
            <div
                className={preview ? `preview-box ${clsName}` : clsName}
                style={{
                    width: this.getContainerWidth(_colSpan),
                }}
                ref={(targetDom) => { this.targetDom = targetDom; }}
            >
                {
                  dropComponent || preview ? (
                        <FormItemRender
                            {...this.props}
                            configs={configs}
                        />
                    ) : <Icon type="drag" />
                }
                {
                    !preview ? 
                        <Icon
                            type="bars"
                            className="edit-icon icon-bars"
                            onClick={this.onShowEditItem}
                        /> : null
                }
                <FormItemEdit
                    inForm
                    visible={visible}
                    onDelete={this.onDeleteChildren}
                    onUpdateConfigs={this.onUpdateConfigs}
                    colSpanArr={colSpanArr}
                    formItemArr={formItemArr}
                    configs={configs}
                    dropComponent={dropComponent}
                    layoutColumn={layoutColumn}
                    onDeleteContainer={() => {
                        this.setState({
                            visible: false,
                        }, () => {
                            onDeleteFormItemContainer(colIndexNum);
                        });
                    }}
                    onClose={this.onClose}
                />
            </div>
        );
    }
}

export default FormItemContainer;