import React, { PureComponent, Fragment } from 'react';
import { Form, Row, Button, message, Icon } from 'antd';
import FormEdit from '../../EditDrawer/FormEdit';
import getCellColSpan from '../../../utils/CellUtil';
import FormItemContainer from '../FormItemContainer';
import { getFormApi, getSubmitData } from '../../../utils/FormUtil';
import { FORM_CONTAINER, TRUE } from '../../../utils/Constants';
import { isLogin } from '../../../utils/common';
import './index.css';

class FormContainer extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            visible: false,
            fetchLoading: false,
            expand: true, // 
            configs: {
                layoutColumn: 1, // 
                colSpanArr: [],
                formItemArr: [],
            },
        };
        this.onSubmit = this.onSubmit.bind(this);
        this.onAddRow = this.onAddRow.bind(this);
        this.onCollapse = this.onCollapse.bind(this);
        this.onDeleteRow = this.onDeleteRow.bind(this);
        this.onResetForm = this.onResetForm.bind(this);
        this.onAddRowCell = this.onAddRowCell.bind(this);
        this.onShowEditForm = this.onShowEditForm.bind(this);
        this.onCloseEditForm = this.onCloseEditForm.bind(this);
        this.onUpdateConfigs = this.onUpdateConfigs.bind(this);
        this.onDeleteContainer = this.onDeleteContainer.bind(this);
        this.onUpdateFormLayout = this.onUpdateFormLayout.bind(this);
        this.renderFormItemContainer = this.renderFormItemContainer.bind(this);
        this.onUpdateFormItemConfigs = this.onUpdateFormItemConfigs.bind(this);
        this.onDeleteFormItemContainer = this.onDeleteFormItemContainer.bind(this);
        this.colIndexStart = 0;
    }
    componentDidMount() {
        const { onFormRenderCallBack, form } = this.props;
        if (typeof onFormRenderCallBack === 'function') {
            onFormRenderCallBack(form);
        }
    }
    onResetForm() {
        this.props.form.resetFields();
    }
    onShowEditForm() {
        this.setState({
            visible: true,
            expand: false,
        });
    }
    onCollapse() {
        const { expand } = this.state;
        this.setState({ expand: !expand });
    }
    addNewFormItem(formItemArr, layoutColumnNum, colSpanArr) {
        if (formItemArr.length < layoutColumnNum) {
            for(let i = formItemArr.length; i < layoutColumnNum; i++) {
                formItemArr.push({
                    colIndex: this.colIndexStart + i,
                    colSpan: colSpanArr[i],
                    originSpan: 1,
                });
            }
        }
        return formItemArr;
    }
    onCloseEditForm() {
        this.setState({
            visible: false,
        });
    }
    onAddRowCell() {
        const { configs } = this.state;
        const { colSpanArr } = configs;
        const formItemArr = configs.formItemArr.slice();
        if (formItemArr.length === 0) {
            message.warn('');
            return;
        }
        const rowSpanCount = formItemArr.reduce((total, item, i) => {
            total += item.colSpan;
            if (i === formItemArr.length -1) {
                total %= 24;
            }
            return total;
        }, 0);
        let colSpanIndex = 0;
        let total = 0;
        for (let i = 0; i < colSpanArr.length; i++) {
            total = total + (+colSpanArr[i]);
            if (total > rowSpanCount) {
                colSpanIndex = i;
                i = colSpanArr.length;
            }
        }
        formItemArr.push({ 
            colIndex: this.colIndexStart,
            originSpan: 1,
            colSpan: colSpanArr[colSpanIndex],
        });
        this.colIndexStart += 1;
        const newConfigs = Object.assign({}, configs);
        newConfigs.formItemArr = formItemArr;
        this.setState({
            configs: newConfigs,
        }, () => {
            this.onUpdateConfigs(newConfigs);
        });
    }
    onUpdateConfigs(newConfigs) {
        const { onUpdateLayoutConfig, configs } = this.props;
        onUpdateLayoutConfig({
            layoutIndex: configs.layoutIndex,
            type: FORM_CONTAINER,
            ...newConfigs,
        });
    }
    onAddRow() {
        const { configs } = this.state;
        const formItemArr = configs.formItemArr.slice();
        if (formItemArr.length === 0) {
            message.warn('');
            return;
        }
        const { layoutColumn } = configs;
        for(let i = 0; i < layoutColumn; i++) {
            formItemArr.push({ colIndex: this.colIndexStart + i });
        }
        this.colIndexStart += layoutColumn;
        const newConfigs = Object.assign({}, configs);
        newConfigs.formItemArr = formItemArr;
        this.setState({
            configs: newConfigs,
        }, () => {
            this.onUpdateConfigs(newConfigs);
        });
    }
    onDeleteRow() {
        const { configs } = this.state;
        const { layoutColumn, formItemArr } = configs;
        const newConfigs = Object.assign({}, configs);
        const newFormItemArr = formItemArr.slice();
        if (newFormItemArr.length <= layoutColumn) {
            message.warn('');
            return;
        }
        newFormItemArr.length = Math.max(0, newFormItemArr.length- layoutColumn);
        newConfigs.formItemArr = newFormItemArr;
        this.setState({
            configs: newConfigs,
        }, () => {
            this.onUpdateConfigs(newConfigs);
        });
    }
    /**
     * @desc 
     */
    onDeleteContainer() {
        const { onUpdateLayoutConfig, layoutIndex } = this.props;
        onUpdateLayoutConfig({ layoutIndex, deleteFlag: true });
    }
    onDeleteFormItemContainer(colIndex) {
        const { configs } = this.state;
        const { formItemArr } = configs;
        const newConfigs = Object.assign({}, configs);
        const newFormItemArr = formItemArr.filter((item) => {
            return item.colIndex !== colIndex;
        });
        newConfigs.formItemArr = newFormItemArr;
        this.setState({
            configs: newConfigs,
        }, () => {
            this.onUpdateConfigs(newConfigs);
        });
    }
    onUpdateFormItemConfigs({ deleteFlag = false, ...values }) {
        const { configs } = this.state;
        const { formItemArr, colSpanArr } = configs;
        const newConfigs = Object.assign({}, configs);
        const newFormItemArr = formItemArr.reduce((arr, item) => {
            // 
            if (deleteFlag) {
                if (values.colIndex !== item.colIndex) {
                    arr.push(Object.assign({}, item));
                } else {
                    arr.push({
                        colIndex: values.colIndex,
                        colSpan: values.colSpan,
                        originSpan: values.originSpan,
                    });
                }
            } else {
                if (values.colIndex === item.colIndex) {
                    arr.push(Object.assign({}, item, values));
                } else {
                    arr.push(Object.assign({}, item));
                }
            }
            return arr;
        }, []);
        newFormItemArr.forEach((item) => {
            // 
            if (item.colIndex > values.colIndex) {
                item.colSpan = getCellColSpan(item.colIndex, item.originSpan,
                    newFormItemArr, colSpanArr);
            }
        });
        newConfigs.formItemArr = newFormItemArr;
        this.setState({
            configs: newConfigs,
        }, () => {
            this.onUpdateConfigs(newConfigs);
        });
    }
    /**
     * @desc 
     * @param {object} newConfigs 
     */
    onUpdateFormLayout(newConfigs) {
        const { configs } = this.state;
        const { layoutColumn, colSpanArr } = newConfigs;
        const newState = {};
        const layoutColumnNum = +layoutColumn;
        if (layoutColumnNum) {
            const copyFormItemArr = configs.formItemArr.slice();
            const formItemArr = this.addNewFormItem(copyFormItemArr, layoutColumnNum, colSpanArr);
            formItemArr.forEach((item) => {
                item.colSpan = getCellColSpan(item.colIndex, item.originSpan, formItemArr, colSpanArr);
            });
            this.colIndexStart += layoutColumnNum;
            newConfigs.layoutColumn = layoutColumnNum;
            newConfigs.formItemArr = formItemArr;
            newState.configs = newConfigs;
        }
        this.setState({
            ...newState
        }, () => {
            this.onCloseEditForm();
            this.onUpdateConfigs(newConfigs);
        });
    }
    /**
     * @desc 
     * @param {*} e 
     */
    onSubmit(e) {
        e.preventDefault();
        const { onShowLoginModal, form, execFetchApi, mode, configs } = this.props;
        if (!isLogin()) {
            onShowLoginModal();
            return;
        }
        form.validateFields((err, values) => {
            if (!err) {
                const api = getFormApi(mode, configs);
                if (api) {
                    execFetchApi(api, getSubmitData(configs, values));
                } else {
                    message.warn('formapi');
                }
            }
        });
    }
    getExpandCount() {
        const { preview } = this.props;
        const { formItemArr = [] } = preview ? this.props.configs || {} : this.state.configs;
        let expandCount = 0;
        for (let a of formItemArr) {
            if (a.type === 'Button' && Array.isArray(a.buttonArr)) {
                for (let b of a.buttonArr) {
                    if (b.logicType === 'expand') {
                        expandCount = +(b.expandCount || 0);
                    }
                    break;
                }
            }
        }
        return expandCount;
    }
    renderFormItemContainer() {
        const { form, preview, context } = this.props;
        const { formItemArr = [], layoutColumn, colSpanArr, formItemAlign } = preview ? this.props.configs || {} : this.state.configs;
        this.GuiContext = context.GuiContext;
        const { expand } = this.state;
        const expandCount = this.getExpandCount();
        return formItemArr.map((itemConfigs, i) => {
            const { colIndex, type, hasLinkage } = itemConfigs;
            // 
            if (expandCount > 0 && !expand && (i+1) > expandCount && type !== 'Button') {
                return null;
            }
            // 
            if (hasLinkage === TRUE) {
                const { linkName, condition } = itemConfigs;
                if (form.getFieldValue(linkName) !== condition) {
                    return null;
                }
            }
            return (
                <FormItemContainer
                    key={`ed-${colIndex}`}
                    colIndex={colIndex}
                    configs={itemConfigs}
                    expand={expand}
                    context={context}
                    onResetForm={this.onResetForm}
                    form={form}
                    preview={preview}
                    formItemAlign={formItemAlign}
                    formItemArr={formItemArr}
                    colSpanArr={colSpanArr}
                    layoutColumn={layoutColumn}
                    onCollapse={this.onCollapse}
                    onDeleteFormItemContainer={this.onDeleteFormItemContainer}
                    onUpdateFormItemConfigs={this.onUpdateFormItemConfigs}
                />
            );
        });
    }
    render() {
        const { visible } = this.state;
        const { preview, layerConfig } = this.props;
        const { configs = {} } = preview ? this.props : this.state;
        const { layoutColumn = 1, formItemArr = [] } = configs || {};
        return (
            <Fragment>
                <div className={`form-container container ${preview ? 'preview-box' : 'border-box'}`}>
                    <div className="inner-box">
                        {
                            <Form onSubmit={this.onSubmit}>
                                <Row
                                    gutter={{ md: 24 / layoutColumn, lg: 24, xl: 48 }}
                                >
                                    {this.renderFormItemContainer()}
                                </Row>
                            </Form>
                        }
                        {
                            formItemArr.length > 0 && !preview ? (
                                <div style={{
                                    textAlign: 'center',
                                    marginTop: '10px',
                                    }}
                                >
                                    <Button
                                        ghost
                                        type="primary"
                                        onClick={this.onAddRowCell}
                                    >
                                        +FormItem
                                    </Button>
                                </div>
                                ) : null
                        }
                    </div>
                    {
                        !preview ? <Icon type="form" className="edit-icon icon-form" onClick={this.onShowEditForm} /> : null
                    }
                </div>
                <FormEdit
                    visible={visible}
                    layerConfig={layerConfig}
                    onDeleteContainer={this.onDeleteContainer}
                    onSave={this.onUpdateFormLayout}
                    onClose={this.onCloseEditForm}
                />
            </Fragment>  
        );
    }
}

export default Form.create()(FormContainer);