import React, { Component, Fragment } from 'react';
import { Form, Col } from 'antd';
import renderComponent from '../../../utils/RenderUtil';
import { getStyle, getInitialValue } from '../../../utils/common';

class FormItemRender extends Component {
	render() {
        const { configs, form, context, preview = false,
            colSpanArr = [], formItemAlign, onResetForm } =this.props;
        const { getFieldDecorator } = form;
        const { type, colSpan, label, name, rules = [], required } = configs;
        const md = colSpan || colSpanArr[0] || 8;
        if (!name && type !== 'Button') {
            return null;
        }
        if (required) {
            rules.push({
                required: true, message: `${label}`,
            });
        }
        const styleObj = getStyle(configs);
        const formItemProps = {
            label,
            style: styleObj, 
        };
        if (formItemAlign === 'r') {
            formItemProps.labelCol = { span: 6 };
            formItemProps.wrapperCol = { span: 14 };
        }
		return (
			<Fragment>
				<Col
			        span={md}
                    md={md}
                    sm={24}
			    >
                    {
                        type !== 'Button' ?
                            (
                                <Form.Item {...formItemProps}>
                                    {getFieldDecorator(name, {
                                        rules: rules || [],
                                        initialValue: getInitialValue(configs),
                                    })(
                                        renderComponent(configs, { preview, context })
                                    )}
                                </Form.Item>
                            ) : (
                                <Form.Item style={styleObj}>
                                    {
                                        renderComponent(configs, { preview, context, onResetForm })
                                    }
                                </Form.Item>
                            )
                    }
			      
			    </Col>
		    </Fragment>
	    );
	}
}

export default FormItemRender;