import { PureComponent } from 'react';

class BaseContainer extends PureComponent {
    onUpdateConfigs(newConfigs) {
        const { onUpdateLayoutConfig } = this.props;
        this.setState({
            configs: newConfigs,
        }, () => {
            onUpdateLayoutConfig({
                layoutIndex: this.getContainerIndex(),
                type: this.getContainerType(),
                ...newConfigs,
            });
        });
    }
    onToggleEditDrawer() {
        this.setState({
            visible: !this.state.visible,
        });
    }
    onDeleteContainer() {
        const { onUpdateLayoutConfig } = this.props;
        onUpdateLayoutConfig({
            layoutIndex: this.getContainerIndex(),
            deleteFlag: true,
        });
        this.onToggleEditDrawer();
    }
    getContainerIndex() {
        const { configs } = this.props;
        return configs.layoutIndex;
    }
    getContainerType() {
        const { configs } = this.props;
        return configs.type;
    }
    _render() {
        return null;
    }
    render() {
        return this._render();
    }
}

export default BaseContainer;