import React, { Component, Fragment } from 'react';
import { Icon, message } from 'antd';
import DropRender from './DropRender';
import CompEdit from '../../EditDrawer/CompEdit';
import { activeColor } from '../../../utils/Constants';
import initDropData from '../../../utils/DropUtil';
import isContainer from '../../../utils/common';
import './index.css';

const clsName = 'drop-container';

class DropContainer extends Component {
    constructor(props) {
        super(props);
        this.state = {
            configs: props.configs,
            showEdit: false,
            dropComponent: !!props.configs.type,
        };
        this.onDrop = this.onDrop.bind(this);
        this.onDragenter = this.onDragenter.bind(this);
        this.onToggleEdit = this.onToggleEdit.bind(this);
        this.onUpdateConfigs = this.onUpdateConfigs.bind(this);
        this.btnIndex = 1;
    }
    componentDidMount() {
        if (!this.DropDom || this.props.preview) {
            return;
        }
        this.DropDom.addEventListener("drop", this.onDrop, false);
        this.DropDom.addEventListener("dragleave", this.onDragLeave, false);
        this.DropDom.addEventListener("dragenter", this.onDragenter, false);
    }
    componentWillUnmount() {
        if (this.DropDom && !this.props.preview) {
            this.DropDom.removeEventListener('drop', this.onDrop);
            this.DropDom.removeEventListener('dragleave', this.onDragLeave);
            this.DropDom.removeEventListener("dragenter", this.onDragenter);
        }
    }
    onToggleEdit() {
        this.setState({
            showEdit: !this.state.showEdit,
        });
    }
    onUpdateConfigs({ deleteFlag = false, ...newConfigs }) {
        const { onUpdateLayoutConfig, configs } = this.props;
        const { layoutIndex } = configs;
        const newState = {
            configs: {
                layoutIndex,
            },
        };
        // 
        if (deleteFlag) {
            newState.dropComponent = false;
            newState.configs.cellStyles = newConfigs.cellStyles;
        } else {
            newState.configs = Object.assign({}, this.state.configs, newConfigs);
        }
        this.setState({
            ...newState,
        });
        onUpdateLayoutConfig({
            ...newState.configs,
            deleteFlag,
            layoutIndex,
        });
        if (!isContainer(this.state.configs.type)) {
            this.onToggleEdit();
        }
    }
    onDrop(event) {
        event.preventDefault();
        event.stopPropagation();
        if (event.target.className === clsName) {
            this.resetBg();
            const data = JSON.parse(event.dataTransfer.getData('text/plain'));
            const { onUpdateLayoutConfig, configs = {} } = this.props;
            const initData = initDropData(data, this);
            if (!initData) {
                message.warn('');
                return;
            }
            const { type, ...newConfigs } = initData;
            const { notAllow = [] } = this.props;
            if (notAllow.indexOf(type) >= 0) {
                message.warn('');
                return;
            }
            const { layoutIndex } = configs;
            if (newConfigs) {
                this.setState({
                    configs: {
                        type,
                        layoutIndex,
                        ...newConfigs,
                    },
                    dropComponent: true,
                }, () => {
                    onUpdateLayoutConfig(this.state.configs);
                });
            }
        }
    }
    resetBg() {
        const dropDomArr = document.querySelectorAll('.drop-container');
        if (dropDomArr) {
            dropDomArr.forEach((item) => {
                item.style.background = '';
            });
        }
    }
    onDragenter(event) {
        if (event.target.className === clsName) {
            event.target.style.background = activeColor;
        }
    }
    onDragLeave(event) {
        if (event.target.className === clsName) {
            event.target.style.background = '';
        }
    }
    render() {
        const { dropComponent, configs, showEdit } = this.state;
        const { layerConfig, preview } = this.props;
        const newConfigs = preview ? this.props.configs :
            Object.assign({}, configs);
        return (
            <div
                className="drop-container"
                ref={(DropDom) => { this.DropDom = DropDom; }}
            >
                {
                  dropComponent || preview ? (
                    <Fragment> 
                        <DropRender
                            {...this.props}
                            onUpdateLayoutConfig={this.onUpdateConfigs}
                            configs={newConfigs}
                        />
                        {
                            !isContainer(configs.type) && !preview ? ( <Icon
                                type="bars"
                                className="edit-icon icon-bars"
                                onClick={this.onToggleEdit}
                            />) : null
                        }
                    </Fragment>
                    ) : !preview ? <Icon type="drag" /> : null
                }
                <CompEdit
                    visible={showEdit}
                    configs={configs}
                    layerConfig={layerConfig}
                    inForm={false}
                    onClose={this.onToggleEdit}
                    onUpdateConfigs={this.onUpdateConfigs}
                />
            </div>
        );
    }
}

export default DropContainer;