import React, { Component } from 'react';
import renderComponent from '../../../utils/RenderUtil';

class DropRender extends Component {
	render() {
		const { configs = {}, ...restProps } =this.props;
		const { cellStyles, textAlign = 'center', type } = configs;
		const styleObj = type === 'Button' ? Object.assign({}, cellStyles, { textAlign }) : cellStyles;
		return (
			<div style={styleObj}>
                {
                    renderComponent(configs, restProps)
                }
		    </div>
	    );
	}
}

export default DropRender;