import React, { PureComponent, Fragment } from 'react';
import { FORM_CONTAINER, TABLE_CONTAINER, EMPTY_CONTAINER, BOX_CONTINAER, HEADER_CONTINAER, LINE_CONTINAER } from '../../../utils/Constants';
import FormContainer from '../FormContainer';
import TableContainer from '../TableContainer';
import BoxContainer from '../BoxContainer';
import EmptyContainer from '../EmptyContainer';
import HeaderContainer from '../HeaderContainer';
import LineContainer from '../LineContainer';
import './index.css';

class LayoutContainer extends PureComponent {
    renderContainer() {
        const { layoutConfig, ...restProps } = this.props;
        if (Array.isArray(layoutConfig) && layoutConfig.length > 0) {
            return layoutConfig.map((configs) => {
                const { type, layoutIndex } = configs;
                const itemKey = `layout-${layoutIndex}`;
                if (type === FORM_CONTAINER) {
                    return (<FormContainer key={itemKey} {...restProps} configs={configs} />);
                } else if (type === TABLE_CONTAINER) {
                    return (<TableContainer key={itemKey} {...restProps} configs={configs} />);
                } else if (type === EMPTY_CONTAINER) {
                    return (<EmptyContainer key={itemKey} {...restProps} configs={configs} />);
                } else if (type === BOX_CONTINAER) {
                    return (<BoxContainer key={itemKey} {...restProps} configs={configs} />);
                } else if (type === HEADER_CONTINAER) {
                    return (<HeaderContainer key={itemKey} {...restProps} configs={configs} />);
                } else if (type === LINE_CONTINAER) {
                    return (<LineContainer key={itemKey} {...restProps} configs={configs} />);
                }
                return null;
            });
        }
        return (
            <div className="empty-tips"></div>
        );
    }
    render() {
        return (
            <Fragment>
                {
                    this.renderContainer()
                }
            </Fragment>
        );
    }
}

export default LayoutContainer;